body {
  background-color: #f3f3f3;
  margin: 0;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: border-box;
  font-family: Montserrat, sans-serif;
}

.splash-container {
  height: 66vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.splash-title {
  font-family: Bangers, cursive;
  font-size: 10rem;
}

.splash-battleship-image {
  width: 100%;
  pointer-events: none;
  opacity: .25;
  position: absolute;
  bottom: 5vh;
  left: 20vw;
  transform: rotateY(180deg);
}

.btn {
  font-size: inherit;
  cursor: pointer;
  color: #333;
  background-color: #ff8000;
  border: none;
  border-radius: .2em;
  outline: none;
  padding: .5em 1em;
  text-decoration: none;
}

.btn:hover, .btn:focus {
  background-color: #c60;
}

.splash-btn {
  margin-left: 2rem;
  font-size: 2rem;
}

.splash-btn:first-child {
  margin-left: 0;
}

.container {
  width: 100%;
  justify-content: center;
  display: flex;
}

.battleship-grid {
  background-color: #0af;
  grid-template-rows: repeat(10, 4.6vmin);
  grid-template-columns: repeat(10, 4.6vmin);
  margin: 2vmin;
  display: grid;
}

.grid-computer > .taken, .grid-computer > .boom {
  background-color: #0af !important;
  border-radius: 0 !important;
}

.taken, .ship {
  background-color: #ccc;
  position: relative;
}

.taken.start.vertical, .taken.start.vertical:before {
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
}

.taken.end.vertical, .taken.end.vertical:before {
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}

.taken.start.horizontal, .taken.start.horizontal:before {
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.taken.end.horizontal, .taken.end.horizontal:before {
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
}

.taken.vertical:before, .taken.horizontal:before {
  content: "";
  border: .3vmin solid #fff;
  position: absolute;
  inset: -1px;
}

.taken.horizontal:before {
  border-left: none;
  border-right: none;
  animation: ripplesY 3s linear infinite;
}

.taken.vertical:before {
  border-top: none;
  border-bottom: none;
  animation: ripplesX 3s linear infinite;
}

@keyframes ripplesX {
  0% {
    opacity: 1;
    transform: scaleX(1);
  }

  100% {
    opacity: 0;
    transform: scaleX(1.5);
  }
}

@keyframes ripplesY {
  0% {
    opacity: 1;
    transform: scaleY(1);
  }

  100% {
    opacity: 0;
    transform: scaleY(1.5);
  }
}

.grid-display {
  display: flex;
}

.ship > div {
  width: 4.6vmin;
  height: 4.6vmin;
}

.ship {
  width: calc(4.6vmin * var(--width, 1));
  height: calc(4.6vmin * var(--height, 1));
  border-radius: 2.3vmin;
  flex-wrap: wrap;
  margin: 1vmin;
  display: flex;
}

.battleship-grid div {
  border: 1px solid #fff3;
}

.destroyer-container {
  --width: 2;
}

.destroyer-container-vertical {
  --height: 2;
  --width: 1;
}

.submarine-container, .cruiser-container {
  --width: 3;
}

.submarine-container-vertical, .cruiser-container-vertical {
  --height: 3;
  --width: 1;
}

.battleship-container {
  --width: 4;
}

.battleship-container-vertical {
  --height: 4;
  --width: 1;
}

.carrier-container {
  --width: 5;
}

.carrier-container-vertical {
  --height: 5;
  --width: 1;
}

.hidden-info {
  flex-direction: column;
  align-items: center;
  font-size: 1.5rem;
}

.info-text {
  margin: 1rem;
}

.miss, .boom {
  justify-content: center;
  align-items: center;
  display: flex;
}

.boom:after, .miss:after {
  content: "";
  width: 2vmin;
  height: 2vmin;
  border-radius: 100%;
  position: absolute;
}

.miss:after {
  background-color: #fff;
}

.boom:after {
  background-color: red;
}

.miss:before {
  content: "";
  width: 2vmin;
  height: 2vmin;
  border: 1vmin solid #fff;
  border-radius: 100%;
  animation: hit .2s ease-out forwards;
  position: absolute;
}

.boom {
  animation: boom .2s ease-out forwards;
}

@keyframes hit {
  0% {
    opacity: 1;
    transform: scale(0);
  }

  100% {
    opacity: 0;
    transform: scale(4);
  }
}

@keyframes boom {
  0% {
    background-color: red;
  }

  100% {
    background-color: #ccc;
  }
}

.player {
  margin: 2vmin;
}

.connected, .ready {
  opacity: .25;
  font-weight: normal;
  text-decoration: line-through;
}

.connected.active, .ready.active {
  opacity: 1;
  text-decoration: none;
}

.your-go .battleship-grid.grid-computer {
  border: 5px solid #ff0;
}

.enemys-go .battleship-grid.grid-user {
  border: 5px solid red;
}

.chat {
  background: #fff;
  border-radius: 5px;
  padding: 5px;
}

.chat:empty, #info:empty {
  display: none;
}

#info {
  color: #fff;
  background: #0af;
  padding: 25px;
}

.field-name {
  text-align: center;
  font-weight: bolder;
}

